import FileUpload from "@/components/FileUpload";
import Menu from "@/components/Menu";

function getBackground(isDarkMode: boolean) {
  return isDarkMode
    ? "bg-gradient-to-br from-gray-900 via-gray-800 to-gray-700"
    : "bg-gradient-to-br from-blue-100 via-white to-blue-200";
}

export default function Home() {
  // Simple dark mode toggle (SSR-friendly, no localStorage)
  const isDarkMode =
    typeof window !== "undefined"
      ? window.matchMedia("(prefers-color-scheme: dark)").matches
      : false;

  return (
    <div className={`relative min-h-screen ${getBackground(isDarkMode)} transition-colors duration-300`}>
      <div className="relative text-gray-900 dark:text-white">
        <header>
          <Menu />
        </header>
        <FileUpload />
        <footer>
          <div className="flex h-[5vh] justify-center items-center h-16 border-gray-800 shadow-2xl bg-black bg-opacity-65 text-white dark:bg-gray-900 dark:text-gray-200">
            <p className="opacity-100 drop-shadow-xl">Data Visualise App | by Miko≈Çaj Karla</p>
          </div>
        </footer>
      </div>
    </div>
  );
}